import VestingField from './vesting/VestingField';
import { Control, Controller } from 'react-hook-form';
import { DeployFormFields } from 'modules/launchpool/models/DeployFormModels';

const DeployFormPartVesting = ({
  control,
}: {
  control: Control<DeployFormFields>;
}) => {
  return (
    <Controller
      control={control}
      name='vesting'
      render={({ field: { onChange, value }, fieldState: { error } }) => (
        <VestingField
          label='Vesting schedule'
          value={value}
          onChange={onChange}
          error={error?.message}
        />
      )}
    ></Controller>
  );
};

export default DeployFormPartVesting;
