import { makeAutoObservable } from 'mobx';

export enum NotificationType {
  Success = 'success',
  Warning = 'warning',
  Error = 'error',
}

type Notification = {
  id: number;
  text: string;
  type: NotificationType;
};

export class NotificationStore {
  constructor() {
    makeAutoObservable(this);
  }

  private _idCounter = 0;
  private _notifications: Notification[] = [];
  private _notificationLifeSeconds = 5;

  get notifications() {
    return this._notifications;
  }

  get notificationLifeSeconds() {
    return this._notificationLifeSeconds;
  }

  addNotification({ text, type }: { text: string; type: NotificationType }) {
    const notification = {
      id: this._idCounter++,
      text: text,
      type: type,
    };

    this._notifications.push(notification);

    setTimeout(() => {
      this.removeNotication(notification);
    }, this._notificationLifeSeconds * 1000);
  }

  private removeNotication(notification: Notification) {
    this._notifications = this._notifications.filter(
      (x) => x.id !== notification.id
    );
  }
}
