import { observer } from 'mobx-react-lite';
import { useLayoutStore } from 'providers/LayoutStoreProvider';
import { Modal } from 'react-bootstrap';

const ContentModal = observer(() => {
  const layoutStore = useLayoutStore();
  return (
    <Modal
      size='sm'
      show={layoutStore.contentModal.show}
      backdrop='static'
      keyboard={false}
      onHide={() => layoutStore.hideContentModal()}
      centered
    >
      <Modal.Body>{layoutStore.contentModal.content}</Modal.Body>
    </Modal>
  );
});

const ModalArea = () => {
  return (
    <>
      <ContentModal />
    </>
  );
};

export default ModalArea;
