import './index.scss';
import { Button, Container, Navbar as BootstrapNavbar } from 'react-bootstrap';
import { observer } from 'mobx-react-lite';
import { cutString } from 'utils/strings';
import { FiLogOut } from 'react-icons/fi';
import { useCallback } from 'react';
import { useEverStore } from 'providers/EverStoreProvider';

const Navbar = observer(() => {
  const auth = useEverStore();

  const onEverLoginClick = useCallback(() => {
    auth.login();
  }, [auth]);

  const onEverLogoutClick = useCallback(() => {
    auth.logout();
  }, [auth]);

  return (
    <>
      <BootstrapNavbar className='m-0 p-0 bg-navbar navbar-container'>
        <Container
          fluid='xl'
          className='py-2 py-sm-4 d-flex justify-content-between'
        >
          <BootstrapNavbar.Brand className='m-0 p-0' />

          {auth.loggedIn && auth.account?.address ? (
            <div className='d-flex align-items-center text-nowrap my-1'>
              <span className='navbar-address text-decoration-none'>
                {cutString(auth.account.address, 8, 4)}
              </span>
              <FiLogOut
                className='ms-3 ms-sm-4 fs-3 text-primary cursor-pointer'
                onClick={onEverLogoutClick}
              />
            </div>
          ) : (
            <Button
              className='ms-3 px-4 fs-6 fw-light'
              variant='primary'
              onClick={onEverLoginClick}
            >
              <span>Connect EVER wallet</span>
            </Button>
          )}
        </Container>
      </BootstrapNavbar>
    </>
  );
});

export default Navbar;
