import * as yup from 'yup';
import { getUrlValidation } from 'utils/validation';
import { yupResolver } from '@hookform/resolvers/yup/dist/yup';
import { Controller, useForm } from 'react-hook-form';
import InputField from 'components/core/input-field/InputField';
import { BsPlusCircleFill } from 'react-icons/bs';
import { useCallback, useMemo } from 'react';
import { SocialInfo } from 'modules/launchpool/models/DeployFormModels';
import InputSelect, {
  InputSelectOption,
} from 'components/core/input-select/InputSelect';

type SocialFormFields = {
  name?: InputSelectOption;
  url?: string;
};

const SocialFormValidationSchema = yup.object({
  name: yup.object({}),
  url: getUrlValidation({}),
});

const allAvailableSelectValues: InputSelectOption[] = [
  { label: 'Twitter', value: 'twitter' },
  { label: 'Discord', value: 'discord' },
  { label: 'Telegram', value: 'telegram' },
  { label: 'Facebook', value: 'facebook' },
  { label: 'Instagram', value: 'instagram' },
  { label: 'Reddit', value: 'reddit' },
  { label: 'Medium', value: 'medium' },
];

const SocialForm = ({
  onAdd,
  selected,
}: {
  onAdd: (val: SocialInfo) => void;
  selected?: string[];
}) => {
  const { handleSubmit, control, reset } = useForm<SocialFormFields>({
    resolver: yupResolver(SocialFormValidationSchema),
    mode: 'onChange',
    defaultValues: {},
  });

  const selectValues = useMemo(
    () => [
      ...allAvailableSelectValues.filter(
        (x) => !selected?.find((name) => x.value === name)
      ),
    ],
    [selected]
  );

  const onFormSubmit = useCallback(
    (data: SocialFormFields) => {
      if (!data.name || !data.url) {
        return;
      }

      onAdd({ name: data.name.value, url: data.url });

      reset();
    },
    [onAdd, reset]
  );

  if (selectValues.length === 0) {
    return null;
  }

  return (
    <div className='d-flex flex-wrap gap-3'>
      <div style={{ minWidth: '150px' }}>
        <Controller
          control={control}
          name='name'
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <InputSelect
              label='Network'
              value={value}
              onChange={onChange}
              error={error?.message}
              options={selectValues}
            />
          )}
        ></Controller>
      </div>

      <div style={{ minWidth: '300px' }}>
        <Controller
          control={control}
          name='url'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <InputField
              label='URL'
              value={value || ''}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </div>

      <BsPlusCircleFill
        className='fs-3 text-success cursor-pointer mx-3'
        style={{ marginTop: '37px' }}
        onClick={handleSubmit(onFormSubmit)}
      />
    </div>
  );
};

export default SocialForm;
