import * as yup from 'yup';
import InputField from 'components/core/input-field/InputField';
import { Control, Controller } from 'react-hook-form';
import { getUrlValidation } from 'utils/validation';
import InputArea from 'components/core/input-area/InputArea';
import { DeployFormFields } from 'modules/launchpool/models/DeployFormModels';

const maxLengthMessage = 'Are you sure? ( ͡° ͜ʖ ͡°)';

export const DeployFormValidationProjectSchema = yup.object({
  name: yup.string().max(1000, maxLengthMessage),
  description: yup.string(),
  imageUrl: getUrlValidation({}).max(1000, maxLengthMessage),
  landingUrl: getUrlValidation({}).max(1000, maxLengthMessage),
});

const DeployFormPartProject = ({
  control,
}: {
  control: Control<DeployFormFields>;
}) => {
  return (
    <div className='d-flex flex-column gap-3'>
      <Controller
        control={control}
        name='project.name'
        render={({
          field: { onChange, onBlur, value },
          fieldState: { error },
        }) => (
          <InputField
            label='Project name'
            value={value || ''}
            onChange={onChange}
            onBlur={onBlur}
            error={error?.message}
          />
        )}
      ></Controller>

      <Controller
        control={control}
        name='project.description'
        render={({
          field: { onChange, onBlur, value },
          fieldState: { error },
        }) => (
          <InputArea
            label='Project description'
            value={value || ''}
            onChange={onChange}
            onBlur={onBlur}
            error={error?.message}
          />
        )}
      ></Controller>

      <Controller
        control={control}
        name='project.imageUrl'
        render={({
          field: { onChange, onBlur, value },
          fieldState: { error },
        }) => (
          <InputField
            label='Image Url'
            value={value || ''}
            onChange={onChange}
            onBlur={onBlur}
            error={error?.message}
          />
        )}
      ></Controller>

      <Controller
        control={control}
        name='project.landingUrl'
        render={({
          field: { onChange, onBlur, value },
          fieldState: { error },
        }) => (
          <InputField
            label='Landing Url'
            value={value || ''}
            onChange={onChange}
            onBlur={onBlur}
            error={error?.message}
          />
        )}
      ></Controller>
    </div>
  );
};

export default DeployFormPartProject;
