import 'react-datetime/css/react-datetime.css';
import './InputDate.scss';
import moment from 'moment';
import Datetime from 'react-datetime';
import { useCallback, useMemo } from 'react';

type InputDateProps = {
  label?: string;
  value?: Date;
  error?: string;
  fromToday?: boolean;
  className?: string;
  onChange?: (date: Date | null) => void;
};

const InputDate = (props: InputDateProps) => {
  const onChange = useCallback(
    (val: string | moment.Moment) => {
      if (typeof val === 'string') {
        props.onChange?.(null);
        return;
      }

      props.onChange?.(val.toDate());
    },
    [props]
  );

  const todayStart = useMemo(() => moment().utc().startOf('day'), []);

  let inputProps = {} as any;

  if (!props.value) {
    inputProps.value = '';
  }

  const isValidDate = useCallback(
    (current: moment.Moment) => {
      if (!props.fromToday) {
        return true;
      }

      return current.isSameOrAfter(todayStart);
    },
    [props.fromToday, todayStart]
  );

  return (
    <div className='d-flex flex-column'>
      {props.label && <div className='mb-2'>{props.label}</div>}

      <Datetime
        className={`input-date ${props.error ? 'with-error' : ''}`}
        value={props.value}
        onChange={onChange}
        dateFormat='DD.MM.YYYY'
        timeFormat='HH:mm'
        isValidDate={isValidDate}
        inputProps={inputProps}
        closeOnSelect
        utc
      />

      {props.error && (
        <span className='d-flex text-danger fs-6'>{props.error}</span>
      )}
    </div>
  );
};

export default InputDate;
