import { Abi } from 'abi/abi';
import BigNumber from 'bignumber.js';
import { Address } from 'everscale-inpage-provider';
import { makeAutoObservable } from 'mobx';
import { DeployLaunchPoolDTO } from 'modules/launchpool/models/LaunchPoolFactoryModels';
import { EverStore } from './EverStore';

export type ContractDetails = {
  address: string;
  owner?: string;
  pendingOwner?: string;
};

export class LaunchPoolFactoryStore {
  constructor(private everStore: EverStore) {
    makeAutoObservable(this);
  }

  private rpcClient = this.everStore.getEverProvider();

  async getContractDetails(
    address?: string | undefined | null
  ): Promise<ContractDetails | null> {
    if (!address || !this.everStore.loggedIn) {
      return null;
    }

    try {
      const launchPoolFactoryContract = new this.rpcClient.Contract(
        Abi.LaunchPoolFactory,
        new Address(address)
      );

      const ownerRequest = await launchPoolFactoryContract.methods
        .owner({})
        .call();

      const pendingOwnerRequest = await launchPoolFactoryContract.methods
        .pending_owner({})
        .call();

      const ownerAddress = ownerRequest.owner.toString();
      const pendingOwnerAddress = pendingOwnerRequest.pending_owner.toString();

      return {
        address: address,
        owner: ownerAddress,
        pendingOwner: pendingOwnerAddress,
      };
    } catch (e) {
      console.error(e);

      return {
        address: address,
      };
    }
  }

  async acceptOwnership(address: string) {
    if (!this.everStore.loggedIn) {
      return;
    }

    const myAddress = this.everStore.account?.address.toString();

    if (!myAddress) {
      return;
    }

    const launchPoolFactoryContract = new this.rpcClient.Contract(
      Abi.LaunchPoolFactory,
      new Address(address)
    );

    const result = await launchPoolFactoryContract.methods
      .acceptOwner({} as never)
      .sendWithResult({
        from: new Address(myAddress),
        amount: new BigNumber(1)
          .multipliedBy(new BigNumber(10).pow(9))
          .toString(),
      });

    return result;
  }

  async deployLaunchPool(address: string, data: DeployLaunchPoolDTO) {
    if (!this.everStore.loggedIn) {
      return;
    }

    const myAddress = this.everStore.account?.address.toString();

    if (!myAddress) {
      return;
    }
    const launchPoolFactoryContract = new this.rpcClient.Contract(
      Abi.LaunchPoolFactory,
      new Address(address)
    );

    const result = await launchPoolFactoryContract.methods
      .deployLaunchPool(data)
      .sendWithResult({
        from: new Address(myAddress),
        amount: new BigNumber(1)
          .multipliedBy(new BigNumber(10).pow(9))
          .toString(),
      });

    return result;
  }
}
