import * as yup from 'yup';
import {
  getAddressValidation,
  getDateValidation,
  getNumberValidation,
} from 'utils/validation';
import CopiableField from 'components/core/copiable-field/CopiableField';
import InputDate from 'components/core/input-date/InputDate';
import InputField from 'components/core/input-field/InputField';
import { Row, Col } from 'react-bootstrap';
import { Control, Controller } from 'react-hook-form';
import { DeployFormFields } from 'modules/launchpool/models/DeployFormModels';

export const DeployFormValidationCoreSchema = yup.object({
  poolOwner: getAddressValidation({ required: true }),
  startTime: getDateValidation({ required: true }),
  endTime: getDateValidation({ required: true }),
  softCap: getNumberValidation({
    min: 0,
    required: true,
  }),
  hardCap: getNumberValidation({
    min: 0,
    required: true,
  }),
  limitOnDeposit: getNumberValidation({
    min: 0,
    required: true,
  }),
  sendGasTo: getAddressValidation({ required: true }),
});

const DeployFormPartCore = ({
  control,
}: {
  control: Control<DeployFormFields>;
}) => {
  return (
    <Row className='gy-3'>
      <Col xs={12}>
        <Controller
          control={control}
          name='core.poolOwner'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <InputField
              label='Pool Owner address'
              value={value || ''}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12} md={6}>
        <Controller
          control={control}
          name='core.startTime'
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <InputDate
              label='Start time (UTC)'
              value={value}
              onChange={onChange}
              error={error?.message}
              className='w-100'
              fromToday
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12} md={6}>
        <Controller
          control={control}
          name='core.endTime'
          render={({ field: { onChange, value }, fieldState: { error } }) => (
            <InputDate
              label='End time (UTC)'
              value={value}
              onChange={onChange}
              error={error?.message}
              className='w-100'
              fromToday
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12} md={4}>
        <Controller
          control={control}
          name='core.softCap'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <InputField
              label='Soft Cap'
              value={value || ''}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12} md={4}>
        <Controller
          control={control}
          name='core.hardCap'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <InputField
              label='Hard Cap'
              value={value || ''}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12} md={4}>
        <Controller
          control={control}
          name='core.limitOnDeposit'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <InputField
              label='Limit on deposit'
              value={value || ''}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12}>
        <Controller
          control={control}
          name='core.sendGasTo'
          render={({ field: { value } }) => (
            <div className='d-flex flex-column'>
              <span>Send gas to</span>
              <CopiableField value={value} />
            </div>
          )}
        ></Controller>
      </Col>
    </Row>
  );
};

export default DeployFormPartCore;
