import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup/dist/yup';
import React, { useCallback, useEffect } from 'react';
import { Button, Card } from 'react-bootstrap';
import { useForm } from 'react-hook-form';
import { observer } from 'mobx-react-lite';
import { useLayoutStore } from 'providers/LayoutStoreProvider';
import { useEverStore } from 'providers/EverStoreProvider';
import Confirmation from 'components/core/modals/Confirmation';
import DeployFormPartCore, {
  DeployFormValidationCoreSchema,
} from './DeployFormPartCore';
import DeployFormPartVesting from './DeployFormPartVesting';
import DeployFormPartProject, {
  DeployFormValidationProjectSchema,
} from './DeployFormPartProject';
import DeployFormPartSocial from './DeployFormPartSocial';
import { DeployFormFields } from 'modules/launchpool/models/DeployFormModels';
import DeployFormPartTokens from './DeployFormPartTokens';

const DeployFormValidationSchema = yup.object({
  core: DeployFormValidationCoreSchema,
  project: DeployFormValidationProjectSchema,
  vesting: yup.array().required(),
  social: yup.array().required(),
});

type DeployFormProps = {
  contract: string;
  onSubmit: (address: string, data: DeployFormFields) => void;
  onReset: () => void;
  isPending: boolean;
};

const DeployForm = observer(
  ({ contract, onSubmit, onReset, isPending }: DeployFormProps) => {
    const everStore = useEverStore();
    const layoutStore = useLayoutStore();
    const { handleSubmit, control, reset, watch } = useForm<DeployFormFields>({
      resolver: yupResolver(DeployFormValidationSchema),
      mode: 'onChange',
      defaultValues: {
        tokens: {},
        core: {
          sendGasTo: everStore.account?.address,
        },
        project: {},
        vesting: [],
        social: [],
      },
    });

    const submitForm = useCallback(
      (data: DeployFormFields) => {
        onSubmit(contract, data);
      },
      [contract, onSubmit]
    );

    const resetForm = useCallback(() => {
      layoutStore.showContentModal(
        <Confirmation
          header='Reset'
          text='Are you sure you want to reset the form?'
          onCancel={() => layoutStore.hideContentModal()}
          onConfirm={() => {
            layoutStore.hideContentModal();
            reset();
            onReset();
          }}
        />
      );
    }, [layoutStore, onReset, reset]);

    useEffect(() => {
      reset();
    }, [contract, reset]);

    return (
      <div className='d-flex flex-column gap-3'>
        <Card>
          <Card.Body>
            <DeployFormPartTokens contract={contract} control={control} />
          </Card.Body>
        </Card>

        {watch('tokens.paymentTokenRoot') && watch('tokens.launchTokenRoot') && (
          <React.Fragment>
            <Card>
              <Card.Body>
                <DeployFormPartCore control={control} />
              </Card.Body>
            </Card>

            <Card>
              <Card.Body>
                <DeployFormPartVesting control={control} />
              </Card.Body>
            </Card>

            <Card>
              <Card.Body>
                <DeployFormPartProject control={control} />
              </Card.Body>
            </Card>

            <Card>
              <Card.Body>
                <DeployFormPartSocial control={control} />
              </Card.Body>
            </Card>

            <Card>
              <Card.Body>
                <div className='d-flex align-items-center gap-3'>
                  <Button
                    variant='secondary'
                    className='px-3'
                    disabled={isPending}
                    onClick={resetForm}
                  >
                    Reset
                  </Button>
                  <Button
                    variant='primary'
                    className='px-4'
                    disabled={isPending}
                    onClick={handleSubmit(submitForm)}
                  >
                    {!isPending ? 'Deploy Launchpool' : 'Pending...'}
                  </Button>
                </div>
              </Card.Body>
            </Card>
          </React.Fragment>
        )}
      </div>
    );
  }
);

export default DeployForm;
