import { Button } from 'react-bootstrap';

const Confirmation = ({
  header,
  text,
  onConfirm,
  onCancel,
}: {
  header?: string;
  text?: string;
  onConfirm: () => void;
  onCancel: () => void;
}) => {
  return (
    <div className='text-center p-2'>
      {header && <h3 className='mx-4 mb-3 fs-4 text-center'>{header}</h3>}

      {text && <h5 className='px-4 my-4 fs-6 fw-normal'>{text}</h5>}

      <div className='d-flex justify-content-between mt-3'>
        <Button variant='secondary' onClick={onCancel} style={{ width: 120 }}>
          <span>Cancel</span>
        </Button>

        <Button variant='primary' onClick={onConfirm} style={{ width: 120 }}>
          <span>Yes</span>
        </Button>
      </div>
    </div>
  );
};

export default Confirmation;
