import * as yup from 'yup';
import { yupResolver } from '@hookform/resolvers/yup/dist/yup';
import InputField from 'components/core/input-field/InputField';
import { useCallback, useEffect } from 'react';
import { Card, Button } from 'react-bootstrap';
import { useForm, SubmitHandler, Controller } from 'react-hook-form';
import { getAddressValidation } from 'utils/validation';

type ContractAddressFormFields = {
  contractAddress: string;
};

const ContractAddressFormSchema = yup.object({
  contractAddress: getAddressValidation({ required: true }),
});

type ContractAddressFormProps = {
  contract: string | undefined;
  onChange: (val: string) => void;
};

const ContractAddressForm = ({
  contract,
  onChange,
}: ContractAddressFormProps) => {
  const { handleSubmit, reset, control } = useForm<ContractAddressFormFields>({
    resolver: yupResolver(ContractAddressFormSchema),
    mode: 'onChange',
    defaultValues: {
      contractAddress: contract,
    },
  });

  const applyForm = useCallback<SubmitHandler<ContractAddressFormFields>>(
    (data) => {
      if (data.contractAddress === contract) {
        return;
      }

      onChange(data.contractAddress);
    },
    [onChange, contract]
  );

  useEffect(() => {
    reset({ contractAddress: contract });
  }, [contract, reset]);

  return (
    <Card>
      <Card.Body>
        <Controller
          control={control}
          name='contractAddress'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <InputField
              label='Contract address'
              value={value || ''}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>

        <Button
          variant='primary'
          className='px-4 mt-3'
          onClick={handleSubmit(applyForm)}
        >
          Set
        </Button>
      </Card.Body>
    </Card>
  );
};
export default ContractAddressForm;
