import { CopyToClipboard } from 'react-copy-to-clipboard';
import { Button } from 'react-bootstrap';
import { IoMdCopy } from 'react-icons/io';
import { useMemo } from 'react';
import { cutString } from 'utils/strings';
import { useNotification } from 'providers/NotificationProvider';
import { NotificationType } from 'stores/NotificationStore';

interface IInputReadonlyProps {
  value?: string;
  onCopyMessage?: string;
  maxWidth?: number;
}

const CopiableField = (props: IInputReadonlyProps) => {
  const notificationService = useNotification();
  const copyNotification = useMemo(
    () => ({
      text: props.onCopyMessage ?? '',
      type: NotificationType.Success,
    }),
    [props.onCopyMessage]
  );

  return (
    <div style={{ maxWidth: props.maxWidth }}>
      <div className='d-flex align-items-center justify-content-start'>
        <div className='py-1 px-2 fs-5 fw-bolder text-primary'>
          <span className='d-none d-lg-block'>{props.value}</span>
          <span className='d-block d-lg-none'>
            {cutString(props.value ?? '', 8, 6)}
          </span>
        </div>
        <CopyToClipboard
          text={props.value ?? ''}
          onCopy={() => {
            if (!props.onCopyMessage || props.onCopyMessage === '') {
              return;
            }

            notificationService.addNotification(copyNotification);
          }}
        >
          <Button
            className='d-flex justify-content-center align-items-center p-1 ms-1'
            variant='info'
            style={{ height: '30px', width: '30px' }}
          >
            <IoMdCopy className='fs-4' />
          </Button>
        </CopyToClipboard>
      </div>
    </div>
  );
};

export default CopiableField;
