import { observer } from 'mobx-react-lite';
import { useEverStore } from 'providers/EverStoreProvider';
import { useDeployStore } from '../providers/DeploymentStoreProvider';
import ContractAddressForm from './contract-address/ContractAddressForm';
import WalletNotConnected from './wallet-not-connected/WalletNotConnected';
import DeployForm from './deploy-form/DeployForm';
import { useCallback } from 'react';
import { DeployFormFields } from '../models/DeployFormModels';
import ContractAddressDetails from './contract-address/ContractAddressDetails';
import DeployStatus from './deploy-status/DeployStatus';

const Deploy = observer(() => {
  const everStore = useEverStore();
  const deployStore = useDeployStore();

  const onDeployFormSubmit = useCallback(
    (address: string, data: DeployFormFields) => {
      const deploy = async (address: string, data: DeployFormFields) => {
        await deployStore.deployLaunchpool(address, data);
      };

      deploy(address, data);
    },
    [deployStore]
  );

  const onDeployFormReset = useCallback(() => {
    deployStore.resetDeployState();
  }, [deployStore]);

  if (!everStore.loggedIn) {
    return <WalletNotConnected />;
  }

  return (
    <div className='d-flex flex-column gap-4'>
      <ContractAddressForm
        contract={deployStore.contractAddress}
        onChange={(address) => deployStore.setContract(address)}
      />

      <ContractAddressDetails />

      {deployStore.contractState.isOwner === true &&
        deployStore.contractAddress && (
          <DeployForm
            contract={deployStore.contractAddress}
            onSubmit={onDeployFormSubmit}
            onReset={onDeployFormReset}
            isPending={deployStore.deployState.isPending}
          />
        )}

      {(deployStore.deployState.status || deployStore.deployState.error) && (
        <DeployStatus state={deployStore.deployState} />
      )}
    </div>
  );
});

export default Deploy;
