import { observer } from 'mobx-react-lite';
import { useDeployStore } from 'modules/launchpool/providers/DeploymentStoreProvider';
import React, { useCallback, useState } from 'react';
import { Card, Button } from 'react-bootstrap';

const ContractAddressDetails = observer(() => {
  const deployStore = useDeployStore();

  const [acceptOwnershipPending, setAcceptOwnershipPending] = useState(false);

  const onAcceptOwnership = useCallback(
    (address?: string | undefined) => {
      if (!address) {
        return;
      }

      const accept = async (address: string) => {
        try {
          setAcceptOwnershipPending(true);

          await deployStore.acceptContractOwnership(address);
        } finally {
          setAcceptOwnershipPending(false);
        }
      };

      accept(address);
    },
    [deployStore]
  );

  if (
    !deployStore.contractAddress ||
    deployStore.contractState.isOwner === true
  ) {
    return null;
  }

  return (
    <Card>
      <Card.Body>
        <div className='d-flex flex-column gap-1 fs-6'>
          {deployStore.contractState.isValid === true && (
            <React.Fragment>
              {deployStore.contractState.isOwner === false && (
                <span className='fw-bold text-danger'>
                  You're not the owner.
                </span>
              )}

              {deployStore.contractState.isPendingOwner === true && (
                <Button
                  variant='primary'
                  style={{ maxWidth: 200 }}
                  disabled={acceptOwnershipPending}
                  onClick={() => onAcceptOwnership(deployStore.contractAddress)}
                >
                  <span>
                    {!acceptOwnershipPending
                      ? 'Accept ownership'
                      : 'Pending...'}
                  </span>
                </Button>
              )}
            </React.Fragment>
          )}

          {deployStore.contractState.isValid === false && (
            <span className='fw-bold text-danger'>
              No LaunchPoolFactory information found on this address.
            </span>
          )}
        </div>
      </Card.Body>
    </Card>
  );
});

export default ContractAddressDetails;
