import { Abi } from 'abi/abi';
import { Address } from 'everscale-inpage-provider';
import { makeAutoObservable, runInAction } from 'mobx';
import { EverStore } from './EverStore';

export type Token = {
  address: string;
  decimals: number;
  name: string;
  symbol: string;
};

export class TokenRootStore {
  constructor(private everStore: EverStore) {
    makeAutoObservable(this);
  }

  private tokenByAddressMap = new Map<string, Token>();

  async getTokenByAddress(rootAddress?: string | undefined | null) {
    if (!rootAddress || !this.everStore.loggedIn) {
      return null;
    }

    const token = this.tokenByAddressMap.get(rootAddress);

    if (token) {
      return token;
    }

    try {
      const rpc = this.everStore.getEverProvider();

      const contract = new rpc.Contract(
        Abi.TokenRoot,
        new Address(rootAddress)
      );

      const symbol = (await contract.methods.symbol({ answerId: 0 }).call({}))
        .value0;

      const name = (await contract.methods.name({ answerId: 0 }).call({}))
        .value0;

      const decimals = (
        await contract.methods.decimals({ answerId: 0 }).call({})
      ).value0;

      const token = {
        address: rootAddress,
        symbol: symbol,
        name: name,
        decimals: Number(decimals),
      };

      runInAction(() => {
        this.tokenByAddressMap.set(rootAddress, token);
      });

      return token;
    } catch {
      return null;
    }
  }
}
