import { observer, useLocalObservable } from 'mobx-react-lite';
import { createContext, useContext } from 'react';
import { LaunchPoolFactoryStore } from 'stores/LaunchPoolFactoryStore';
import { useEverStore } from './EverStoreProvider';

const launchPoolFactoryStoreContext = createContext<LaunchPoolFactoryStore>(
  {} as LaunchPoolFactoryStore
);

export const ProvideLaunchPoolFactoryStore = observer(({ children }: any) => {
  const ever = useEverStore();
  const store = useLocalObservable(() => new LaunchPoolFactoryStore(ever));

  return (
    <launchPoolFactoryStoreContext.Provider value={store}>
      {children}
    </launchPoolFactoryStoreContext.Provider>
  );
});

export const useLaunchPoolFactoryStore = () => {
  return useContext(launchPoolFactoryStoreContext);
};
