import { observer } from 'mobx-react-lite';
import { useNotification } from 'providers/NotificationProvider';
import { useEffect, useMemo, useState } from 'react';
import { BsCheckCircle, BsExclamationCircle, BsXCircle } from 'react-icons/bs';
import { NotificationType } from 'stores/NotificationStore';
import './NotificationArea.scss';

const Notification = ({
  item,
}: {
  item: { text: string; type: NotificationType };
}) => {
  const icon = useMemo(() => {
    switch (item.type) {
      case NotificationType.Success:
        return <BsCheckCircle />;
      case NotificationType.Warning:
        return <BsExclamationCircle />;
      case NotificationType.Error:
        return <BsXCircle />;
    }
  }, [item.type]);

  const [show, setShow] = useState(false);

  useEffect(() => {
    const initTimeout = setTimeout(() => setShow(true), 100);

    return () => {
      clearTimeout(initTimeout);
    };
  }, []);

  return (
    <div
      className={`notification_area_item d-flex align-items-center p-2 my-1 ${
        item.type
      } ${show ? 'show' : ''}`}
    >
      <div className='d-flex align-items-center flex-shrink-0'>{icon}</div>
      <span className='ms-2'>{item.text}</span>
    </div>
  );
};

const NotificationArea = observer(() => {
  const notificationService = useNotification();

  return (
    <div className='notification_area pe-4 pe-xl-5'>
      <div className='d-flex flex-column'>
        {notificationService.notifications.map((notification) => (
          <Notification key={notification.id} item={notification} />
        ))}
      </div>
    </div>
  );
});

export default NotificationArea;
