import { observer, useLocalObservable } from 'mobx-react-lite';
import { useEverStore } from 'providers/EverStoreProvider';
import { useLaunchPoolFactoryStore } from 'providers/LaunchPoolFactoryStoreProvider';
import { createContext, useContext, useEffect } from 'react';
import { DeployStore } from '../stores/DeployStore';

const deployStoreContext = createContext<DeployStore>({} as DeployStore);

export const ProvideDeploymentStore = observer(({ children }: any) => {
  const launchPoolFactoryStore = useLaunchPoolFactoryStore();
  const everStore = useEverStore();

  const store = useLocalObservable(
    () => new DeployStore(launchPoolFactoryStore, everStore)
  );

  useEffect(() => {
    const defaultContract = process.env.REACT_APP_LAUNCHPOOL_CONTRACT_ADDRESS;

    if (!defaultContract) {
      return;
    }

    store.setContract(defaultContract);
  }, [store]);

  return (
    <deployStoreContext.Provider value={store}>
      {children}
    </deployStoreContext.Provider>
  );
});

export const useDeployStore = () => {
  return useContext(deployStoreContext);
};
