import './InputSelect.scss';
import Select, { components } from 'react-select';

export type InputSelectOption = {
  label: string;
  value: string;
  icon?: string;
};

type InputSelectProps = {
  label?: string;
  placeholder?: string;
  className?: string;
  isClearable?: boolean;
  isSearchable?: boolean;
  error?: string;
  options?: InputSelectOption[];
  value?: InputSelectOption;
  onChange?: (value?: InputSelectOption) => void;
};

const { Option } = components;

const InputSelect = (props: InputSelectProps) => {
  const CustomOption = (props: any) => (
    <Option {...props}>
      <div className='d-flex align-items-center'>
        {props.data.icon && (
          <img
            src={props.data.icon}
            alt=''
            style={{ width: 24, height: 24 }}
            className='me-2'
          />
        )}
        <span>{props.data.label}</span>
      </div>
    </Option>
  );

  const CustomSelectValue = (props: any) => (
    <components.SingleValue {...props}>
      <div className='d-flex align-items-center'>
        {props.data.icon && (
          <img
            src={props.data.icon}
            alt=''
            style={{ width: 24, height: 24 }}
            className='me-2'
          />
        )}
        <span>{props.data.label}</span>
      </div>
    </components.SingleValue>
  );

  return (
    <div className={props.className ?? ''}>
      <div className='mb-2'>{props.label}</div>
      <Select
        options={props.options}
        value={props.value ?? null}
        className={`input_select_field_container ${
          props.error ? 'with_error' : ''
        }`}
        classNamePrefix='input_select_field'
        placeholder={props.placeholder}
        isClearable={props.isClearable ?? false}
        isSearchable={props.isSearchable ?? false}
        onChange={(val) => props.onChange?.(val ?? undefined)}
        components={{
          Option: CustomOption,
          SingleValue: CustomSelectValue,
          MultiValue: CustomSelectValue,
        }}
      />
      {props.error && (
        <span className='d-flex text-danger fs-6'>{props.error}</span>
      )}
    </div>
  );
};

export default InputSelect;
