import InputField from 'components/core/input-field/InputField';
import { observer } from 'mobx-react-lite';
import { useTokenRootStore } from 'providers/TokenRootStoreProvider';
import { useCallback, useEffect, useState } from 'react';
import { Token } from 'stores/TokenRootStore';

type TokenFieldProps = {
  value?: Token | undefined;
  label?: string | undefined;
  onChange?: (val?: Token | undefined) => void;
  onBlur?: () => void;
  error?: string | undefined;
};

const fetchDelayMs = 250;

const TokenField = observer((props: TokenFieldProps) => {
  const tokenRootStore = useTokenRootStore();

  const [token, setToken] = useState<Token | undefined | null>();
  const [inputValue, setInputValue] = useState<string>(
    props.value?.address ?? ''
  );

  const onInputValueChange = useCallback((val: string) => {
    setInputValue(val);
  }, []);

  const onChangeProp = props.onChange;

  useEffect(() => {
    const fetch = async () => {
      const result = await tokenRootStore.getTokenByAddress(inputValue);
      setToken(result ?? undefined);
      onChangeProp?.(result ?? undefined);
    };

    const timeout = setTimeout(() => {
      fetch();
    }, fetchDelayMs);

    return () => clearTimeout(timeout);
  }, [onChangeProp, inputValue, tokenRootStore]);

  return (
    <div className='d-flex flex-column'>
      {props.label && <div className='mb-2'>{props.label}</div>}

      <div className='fs-6'>
        <span className={`fw-bold mx-1 ${!token ? 'text-danger' : ''}`}>{`${
          token ? `${token.symbol} (${token.name})` : 'Not found'
        }`}</span>
      </div>

      <InputField
        value={inputValue}
        onChange={onInputValueChange}
        onBlur={props.onBlur}
        error={props.error}
      />
    </div>
  );
});

export default TokenField;
