import { useCallback } from 'react';
import { Button } from 'react-bootstrap';

const NoEverWallet = ({ onClose }: { onClose: () => void }) => {
  const onGetEverWalletClick = useCallback(() => {
    window.open('https://l1.broxus.com/everscale/wallet', '_blank');
    onClose();
  }, [onClose]);

  return (
    <div className='d-flex flex-column'>
      <div className='text-primary fs-5'>Please install EVER Wallet</div>
      <div className='text-secondary fs-6 mt-3'>
        You need to download it in order to use this website.
      </div>
      <div className='d-flex justify-content-between mt-3'>
        <Button variant='secondary' onClick={onClose} style={{ width: 120 }}>
          <span>Cancel</span>
        </Button>

        <Button
          variant='primary'
          type='submit'
          style={{ width: 160 }}
          onClick={onGetEverWalletClick}
        >
          <span>Get EVER wallet</span>
        </Button>
      </div>
    </div>
  );
};

export default NoEverWallet;
