import BigNumber from 'bignumber.js';
import { VestingPeriod } from 'modules/launchpool/models/DeployFormModels';
import moment from 'moment';
import { useCallback, useMemo } from 'react';
import { BsXCircleFill } from 'react-icons/bs';
import VestingPeriodForm from './VestingPeriodForm';

type VestingFieldProps = {
  label?: string | undefined;
  error?: string | undefined;
  value?: VestingPeriod[];
  onChange?: (val: VestingPeriod[]) => void;
};

const VestingField = (props: VestingFieldProps) => {
  const percentageTotal = useMemo(
    () =>
      new BigNumber(
        props.value
          ?.map((x) => new BigNumber(x.percent))
          .reduce((x, y) => x.plus(y), new BigNumber(0)) ?? new BigNumber(0)
      ),
    [props.value]
  );

  const remaining = useMemo(
    () => new BigNumber(100).minus(percentageTotal),
    [percentageTotal]
  );

  const onNewValueAdded = useCallback(
    (val: VestingPeriod) => {
      if (!props.onChange) {
        return;
      }

      const newResult = props.value ? [...props.value, val] : [val];

      newResult.sort((x, y) => (x.unfreezeTime > y.unfreezeTime ? 1 : -1));

      props.onChange(newResult);
    },
    [props]
  );

  const onValueRemoved = useCallback(
    (val: VestingPeriod) => {
      if (!props.onChange) {
        return;
      }

      const newResult = props.value
        ? [...props.value.filter((x) => x !== val)]
        : [];

      newResult.sort((x, y) => (x.unfreezeTime > y.unfreezeTime ? 1 : -1));

      props.onChange(newResult);
    },
    [props]
  );

  return (
    <div className='d-flex flex-column gap-3'>
      {props.label && <div>{props.label}</div>}

      <span className='fs-6 fw-bold'>
        <span className='me-1'>Remaining:</span>
        <span
          className={remaining.isEqualTo(0) ? 'text-success' : 'text-danger'}
        >
          {remaining.toString()} %
        </span>
      </span>

      {props.value && props.value.length > 0 && (
        <div className='d-flex flex-wrap gap-3'>
          {props.value?.map((vestingPeriod, index) => (
            <div
              key={index}
              className='d-flex flex-column align-items-center text-center py-1 px-3 border rounded-1 fs-6 fw-bold'
              style={{ position: 'relative', minWidth: 155 }}
            >
              <span>{`${moment
                .utc(vestingPeriod.unfreezeTime)
                .format('DD.MM.YYYY HH:mm')}`}</span>
              <span>{vestingPeriod.percent} %</span>

              <BsXCircleFill
                className='fs-6 cursor-pointer text-danger position-absolute'
                style={{ right: '-2%', top: '-8%' }}
                onClick={() => onValueRemoved(vestingPeriod)}
              />
            </div>
          ))}
        </div>
      )}

      <VestingPeriodForm onAdd={onNewValueAdded} />
    </div>
  );
};

export default VestingField;
