import * as yup from 'yup';
import { getDateValidation, getPercentValidation } from 'utils/validation';
import { yupResolver } from '@hookform/resolvers/yup/dist/yup';
import { Controller, useForm } from 'react-hook-form';
import InputDate from 'components/core/input-date/InputDate';
import InputField from 'components/core/input-field/InputField';
import { BsPlusCircleFill } from 'react-icons/bs';
import { useCallback } from 'react';
import { VestingPeriod } from 'modules/launchpool/models/DeployFormModels';

type VestingPeriodFormFields = {
  unfreezeTime?: Date;
  percent?: number;
};

const VestingPeriodFormValidationSchema = yup.object({
  unfreezeTime: getDateValidation({}),
  percent: getPercentValidation({}),
});

const VestingPeriodForm = ({
  onAdd,
}: {
  onAdd: (val: VestingPeriod) => void;
}) => {
  const { handleSubmit, control, reset } = useForm<VestingPeriodFormFields>({
    resolver: yupResolver(VestingPeriodFormValidationSchema),
    mode: 'onChange',
    defaultValues: {},
  });

  const onFormSubmit = useCallback(
    (data: VestingPeriodFormFields) => {
      if (!data.percent || !data.unfreezeTime) {
        return;
      }

      onAdd({ percent: data.percent, unfreezeTime: data.unfreezeTime });

      reset();
    },
    [onAdd, reset]
  );

  return (
    <div className='d-flex flex-wrap gap-3'>
      <Controller
        control={control}
        name='unfreezeTime'
        render={({ field: { onChange, value }, fieldState: { error } }) => (
          <InputDate
            label='Unfreeze time (UTC)'
            value={value}
            onChange={onChange}
            error={error?.message}
            fromToday
          />
        )}
      ></Controller>

      <Controller
        control={control}
        name='percent'
        render={({
          field: { onChange, onBlur, value },
          fieldState: { error },
        }) => (
          <InputField
            label='Percent'
            value={value || ''}
            onChange={onChange}
            onBlur={onBlur}
            error={error?.message}
          />
        )}
      ></Controller>

      <BsPlusCircleFill
        className='fs-3 text-success cursor-pointer mx-3'
        style={{ marginTop: '37px' }}
        onClick={handleSubmit(onFormSubmit)}
      />
    </div>
  );
};

export default VestingPeriodForm;
