import { observer, useLocalObservable } from 'mobx-react-lite';
import { createContext, useContext, useEffect } from 'react';
import { EverStore } from 'stores/EverStore';
import { useLayoutStore } from './LayoutStoreProvider';

const everStoreContext = createContext({} as EverStore);

export const ProvideEver = observer(({ children }: any) => {
  const layoutStore = useLayoutStore();
  const store = useLocalObservable(() => new EverStore(layoutStore));

  useEffect(() => {
    store.init();
  }, [store]);

  if (!store.initialized) {
    return null;
  }

  return (
    <everStoreContext.Provider value={store}>
      {children}
    </everStoreContext.Provider>
  );
});

export const useEverStore = () => {
  return useContext(everStoreContext);
};
