import { DeployState } from 'modules/launchpool/stores/DeployStore';
import { Card } from 'react-bootstrap';

const DeployStatus = ({ state }: { state: DeployState }) => {
  return (
    <Card>
      <Card.Body>
        {state.status && (
          <div className='fs-6 d-flex flex-column'>
            <span className='fw-bold' style={{ whiteSpace: 'pre-wrap' }}>
              {state.status}
            </span>
          </div>
        )}
        {state.error && (
          <div className='fs-6 d-flex flex-column'>
            <span
              className='fw-bold text-danger'
              style={{ whiteSpace: 'pre-wrap' }}
            >
              {state.error}
            </span>
          </div>
        )}
      </Card.Body>
    </Card>
  );
};

export default DeployStatus;
