import { BrowserRouter as Router } from 'react-router-dom';
import Navbar from './layout/navbar';
import { ProvideEver } from 'providers/EverStoreProvider';
import { ProvideLayout } from 'providers/LayoutStoreProvider';
import { ProvideNotification } from 'providers/NotificationProvider';
import { ProvideTheme } from 'providers/ThemeProvider';
import { ProvideTokenRootStore } from 'providers/TokenRootStoreProvider';
import { ProvideLaunchPoolFactoryStore } from 'providers/LaunchPoolFactoryStoreProvider';
import Launchpool from 'modules/launchpool';

const AppProviders = ({ children }: { children: JSX.Element }) => {
  return (
    <ProvideTheme>
      <ProvideNotification>
        <ProvideLayout>
          <ProvideEver>
            <ProvideLaunchPoolFactoryStore>
              <ProvideTokenRootStore>{children}</ProvideTokenRootStore>
            </ProvideLaunchPoolFactoryStore>
          </ProvideEver>
        </ProvideLayout>
      </ProvideNotification>
    </ProvideTheme>
  );
};

const App = () => {
  return (
    <AppProviders>
      <Router basename={process.env.PUBLIC_URL}>
        <div className='d-flex flex-column min-vh-100'>
          <Navbar />
          <Launchpool />
        </div>
      </Router>
    </AppProviders>
  );
};

export default App;
