import { observer, useLocalObservable } from 'mobx-react-lite';
import { createContext, useContext } from 'react';
import { TokenRootStore } from 'stores/TokenRootStore';
import { useEverStore } from './EverStoreProvider';

const tokenRootStoreContext = createContext<TokenRootStore>(
  {} as TokenRootStore
);

export const ProvideTokenRootStore = observer(({ children }: any) => {
  const ever = useEverStore();
  const store = useLocalObservable(() => new TokenRootStore(ever));

  return (
    <tokenRootStoreContext.Provider value={store}>
      {children}
    </tokenRootStoreContext.Provider>
  );
});

export const useTokenRootStore = () => {
  return useContext(tokenRootStoreContext);
};
