import { SocialInfo } from 'modules/launchpool/models/DeployFormModels';
import { useCallback } from 'react';
import { BsXCircleFill } from 'react-icons/bs';
import SocialForm from './SocialForm';

type SocialFieldProps = {
  label?: string | undefined;
  error?: string | undefined;
  value?: SocialInfo[];
  onChange?: (val: SocialInfo[]) => void;
};

const SocialField = (props: SocialFieldProps) => {
  const onNewValueAdded = useCallback(
    (val: SocialInfo) => {
      if (!props.onChange) {
        return;
      }

      const newResult = props.value ? [...props.value, val] : [val];

      props.onChange(newResult);
    },
    [props]
  );

  const onValueRemoved = useCallback(
    (val: SocialInfo) => {
      if (!props.onChange) {
        return;
      }

      const newResult = props.value
        ? [...props.value.filter((x) => x !== val)]
        : [];

      props.onChange(newResult);
    },
    [props]
  );

  return (
    <div className='d-flex flex-column gap-3'>
      {props.label && <div>{props.label}</div>}

      {props.value && props.value.length > 0 && (
        <div className='d-flex flex-column flex-wrap gap-3'>
          {props.value?.map((social, index) => (
            <div key={index} className='d-flex flex-column gap-1 fs-6'>
              <span className='d-flex align-items-center fw-bold text-uppercase'>
                {social.name}
                <BsXCircleFill
                  className='fs-6 mx-2 cursor-pointer text-danger'
                  onClick={() => onValueRemoved(social)}
                />
              </span>
              <span>{social.url}</span>
            </div>
          ))}
        </div>
      )}

      <SocialForm
        onAdd={onNewValueAdded}
        selected={props.value?.map((x) => x.name)}
      />
    </div>
  );
};

export default SocialField;
