import CopiableField from 'components/core/copiable-field/CopiableField';
import { Row, Col } from 'react-bootstrap';
import { Control, Controller } from 'react-hook-form';
import { DeployFormFields } from 'modules/launchpool/models/DeployFormModels';
import TokenField from './token/TokenField';

const DeployFormPartTokens = ({
  contract,
  control,
}: {
  contract: string;
  control: Control<DeployFormFields>;
}) => {
  return (
    <Row className='gy-3'>
      <Col xs={12}>
        <div className='d-flex flex-column'>
          <span>Contract Address</span>
          <CopiableField value={contract} />
        </div>
      </Col>

      <Col xs={12}>
        <Controller
          control={control}
          name='tokens.launchTokenRoot'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <TokenField
              label='Launch Token address'
              value={value}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </Col>

      <Col xs={12}>
        <Controller
          control={control}
          name='tokens.paymentTokenRoot'
          render={({
            field: { onChange, onBlur, value },
            fieldState: { error },
          }) => (
            <TokenField
              label='Payment Token address'
              value={value}
              onChange={onChange}
              onBlur={onBlur}
              error={error?.message}
            />
          )}
        ></Controller>
      </Col>
    </Row>
  );
};

export default DeployFormPartTokens;
