import * as yup from 'yup';

export const getAddressValidation = ({
  required,
}: {
  required?: boolean | undefined;
}) => {
  let validation = yup.string().matches(/^0:+[\dA-Za-z]{64}$/, {
    excludeEmptyString: true,
    message: 'Incorrect format',
  });
  // .min(64, 'Address is too short')
  // .max(70, 'Address is too long');

  if (required) {
    validation = validation.required('Field is required');
  }

  return validation;
};

export const getDateValidation = ({
  required,
}: {
  required?: boolean | undefined;
}) => {
  let validation = yup.date().typeError('Incorrect format');

  if (required) {
    validation = validation.required('Field is required');
  }

  return validation.nullable();
};

export const getNumberValidation = ({
  min,
  max,
  required,
  integer,
}: {
  min?: number | undefined;
  max?: number | undefined;
  required?: boolean | undefined;
  integer?: boolean | undefined;
}) => {
  let validation = yup.number().typeError('Incorrect format');

  if (integer) {
    validation = validation.integer('Incorrect format');
  }

  if (min != null) {
    validation = validation.min(min, 'Value is too low');
  }

  if (max != null) {
    validation = validation.max(max, 'Value is too high');
  }

  if (required) {
    validation = validation.required('Value is required');
  }

  return validation.nullable().transform(emptyStringToNull);
};

export const getPercentValidation = ({
  required,
}: {
  required?: boolean | undefined;
}) => {
  let validation = yup.number();

  if (required) {
    validation = validation.required('Field is required');
  }

  validation = validation
    .typeError('Incorrect format')
    .min(0, 'Must be positive')
    .max(100, 'Max value: 100')
    .test(
      'maxDigitsAfterDecimal',
      'More than 2 digits after decimal',
      (number) =>
        number ? /^\d+(\.\d{1,2})?$/.test(number.toString() ?? '') : true
    );

  return validation.nullable().transform(emptyStringToNull);
};

export const getUrlValidation = ({
  required,
}: {
  required?: boolean | undefined;
}) => {
  let validation = yup.string();

  if (required) {
    validation = validation.required('Field is required');
  }

  validation = validation.test('is-url-valid', 'URL is not valid', (value) => {
    return isValidUrl(value);
  });

  return validation.nullable().transform(emptyStringToNull);
};

const emptyStringToNull = (value: string, originalValue: string) => {
  if (typeof originalValue === 'string' && originalValue === '') {
    return null;
  }

  return value;
};

const isValidUrl = (url?: string | undefined) => {
  if (!url || url === '') {
    return true;
  }

  try {
    new URL(url);
  } catch {
    return false;
  }

  return true;
};
