import { DeployFormFields } from 'modules/launchpool/models/DeployFormModels';
import { Control, Controller } from 'react-hook-form';
import SocialField from './social/SocialField';

const DeployFormPartSocial = ({
  control,
}: {
  control: Control<DeployFormFields>;
}) => {
  return (
    <Controller
      control={control}
      name='social'
      render={({ field: { onChange, value }, fieldState: { error } }) => (
        <SocialField
          label='Social Links'
          value={value}
          onChange={onChange}
          error={error?.message}
        />
      )}
    ></Controller>
  );
};

export default DeployFormPartSocial;
