import moment from 'moment';
import BigNumber from 'bignumber.js';
import { Address } from 'everscale-inpage-provider';
import { DeployFormFields } from './DeployFormModels';

export class DeployLaunchPoolDTO {
  _poolOwner: Address;
  _launchTokenRoot: Address;
  _paymentTokenRoot: Address;
  _startTime: string;
  _endTime: string;
  _vestingPeriods: { unfreezeTime: number; percent: number }[];
  _softCap: string;
  _hardCap: string;
  _limitOnDeposit: string;
  _sendGasTo: Address;
  _additionalProjectInfo: {
    projectName: string;
    projectDescription: string;
    projectImageUrl: string;
    projectLandingUrl: string;
    projectSocialLinks: { name: string; link: string }[];
  };
  _whiteList: Address[];

  constructor(data: DeployFormFields, paymentTokenDecimals: number) {
    this._poolOwner = new Address(data.core.poolOwner);
    this._launchTokenRoot = new Address(
      data.tokens.launchTokenRoot?.address ?? ''
    );
    this._paymentTokenRoot = new Address(
      data.tokens.paymentTokenRoot?.address ?? ''
    );
    this._startTime = moment(data.core.startTime).unix().toString();
    this._endTime = moment(data.core.endTime).unix().toString();
    this._vestingPeriods = data.vesting.map((vesting) => ({
      unfreezeTime: moment(vesting.unfreezeTime).unix(),
      percent: vesting.percent * 100,
    }));
    this._softCap = new BigNumber(data.core.softCap)
      .multipliedBy(new BigNumber(10).pow(paymentTokenDecimals))
      .toString();
    this._hardCap = new BigNumber(data.core.hardCap)
      .multipliedBy(new BigNumber(10).pow(paymentTokenDecimals))
      .toString();
    this._limitOnDeposit = new BigNumber(data.core.limitOnDeposit)
      .multipliedBy(new BigNumber(10).pow(paymentTokenDecimals))
      .toString();
    this._sendGasTo = new Address(data.core.sendGasTo);
    this._additionalProjectInfo = {
      projectName: data.project.name ?? '',
      projectDescription: data.project.description ?? '',
      projectImageUrl: data.project.imageUrl ?? '',
      projectLandingUrl: data.project.landingUrl ?? '',
      projectSocialLinks: data.social.map((social) => ({
        name: social.name,
        link: social.url,
      })),
    };
    this._whiteList = [];
  }
}
